/* eslint-env browser */
import LinkedIn from './LinkedIn.jsx'
import User from './User.jsx'
import callbackFetch from '../fetch.js'

const { Component, h, render } = window.preact

class App extends Component {
  constructor() {
    super()
    this.state = {
      loading: true,
      error: null,
      auth: {},
    }
    this.postMessageListener = this.postMessageListener.bind(this)
    window.addEventListener('message', this.postMessageListener, false)
  }
  componentDidMount() {
    this.auth()
  }
  auth() {
    this.setState({ loading: true })
    callbackFetch(`${this.props.host}/user`, (error, res) => {
      this.setState({ loading: false })
      if (error) {
        return this.setState({ error })
      }
      const auth = res.json()
      this.setState({ auth })
    }, {
      credentials: 'include',
    })
  }
  /**
   * @param {MessageEvent} event
   */
  postMessageListener(event) {
    const { data, origin } = event
    if (origin != this.props.host) return
    if (data == 'linkedin-signedin') this.auth()
  }
  componentWillUnmount() {
    window.removeEventListener('message', this.postMessageListener)
  }
  render() {
    if (this.state.error)
      return h('div',{},`Error: `,this.state.error)
    if (this.state.loading)
      return h('div',{},`Loading...`)
    if (!this.state.auth.user)
      return h('div',{},`
        `,h(LinkedIn,{host:this.props.host}),`
      `)
    return h('div',{},`
      `,h(User,{...this.state.auth,onSignout:() => {
        this.setState({ auth: {} })
      },host:this.props.host}),`
    `)
  }
}

window['comments'] = ({
  host = 'https://technation.sucks', container = 'preact',
}) => {
  render(h(App,{host:host}), document.getElementById(container))
}